<template>
  <div class="content">
    <div class="aside">关于我们</div>
    <div class="content-right">
      <div class="title">
        当前位置*关于我们
      </div>
      <p>《博乐》成立于2010年，是一家融合了专业技术、先进理念及市场机制的高科技公司。总部位于莆田市体育中心附近。</p>
      <p>公司由多位彩票界资深人士构成，形成了专业的专家顾问团。并吸引了一批求实上进的青年才俊加入到了我们的团队，具有良好的团队精神。我们在企业策划、彩票分析、产品研发、经营管理、市场营销、渠道建设、资源整合等多方面有着丰富的经验和深厚基础。今后我们将立足于中国数千亿元的彩票市场，将应用、开发与服务进行捆绑，将高端、渠道与终端市场进行整合，将彩票行业向多范围、多领域进行拓展；致力于发展成为中国彩票咨询行业翘楚。</p>
      <p>博乐发展有限公司有着庞大的资源优势，我们与多个省市福彩中心、多家报刊媒体、大型门户网站都保持着合作伙伴关系。我们秉承“专业服务+科技创新=一流产品”的企业文化走在行业的浪尖，更好的服务理念服务于彩民。公司本着“让彩民不再孤单、让彩民不再无助、让彩民能够中奖”的经营理念，正稳步走向成熟。在今后的发展中我们基于国内彩票业，放眼国际市场。日后的发展目标是在两年内公司产值达千万以上，并引进国际风险投资，用5年的时间完成上市计划，最终发展成为一家以项目研发、行业拓展及国际合作为主营业务的资本型运作公司。</p>

    </div>
